<template>
    <div>
      <ul>
        <li v-for="comment in comments" :key="comment.id">{{ comment.content }}</li>
      </ul>
      <textarea v-model="newCommentContent" placeholder="Add a comment"></textarea>
      <button @click="addComment">Add Comment</button>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    props: ['postId'],
    data() {
      return {
        comments: [],
        newCommentContent: ''
      };
    },
    methods: {
      fetchComments() {
        axios.get(`/api/posts/${this.postId}/comments`).then(response => {
          this.comments = response.data;
        });
      },
      addComment() {
        axios.post(`/api/comments`, {
          post_id: this.postId,
          content: this.newCommentContent,
          user_id: 1 // Hardcoded for simplicity, change as needed
        }).then(() => {
          this.fetchComments();
          this.newCommentContent = '';
        });
      }
    },
    mounted() {
      this.fetchComments();
    }
  };
  </script>
  