<template>
    <div>
      <h2>{{ post.title }}</h2>
      <p>{{ post.content }}</p>
      <h3>Comments:</h3>
      <comment-section :postId="post.id"></comment-section>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  import CommentSection from './CommentSection.vue';
  
  export default {
    data() {
      return {
        post: {}
      };
    },
    components: {
      CommentSection
    },
    methods: {
      fetchPost() {
        const postId = this.$route.params.id;
        axios.get(`/api/posts/${postId}`).then(response => {
          this.post = response.data;
        });
      }
    },
    mounted() {
      this.fetchPost();
    }
  };
  </script>
  