<template>
    <div>
      <h1>Creează o postare nouă</h1>
      <form @submit.prevent="submitPost">
        <div>
          <label for="title">Titlu:</label>
          <input type="text" v-model="title" id="title" required />
        </div>
        <div>
          <label for="content">Conținut:</label>
          <textarea v-model="content" id="content" required></textarea>
        </div>
        <button type="submit">Creează Postare</button>
      </form>
    </div>
  </template>
  
  <script>
  import { createPost } from '../services/api';
  
  export default {
    data() {
      return {
        title: '',
        content: '',
        userId: 1 // Ex: ID-ul unui utilizator logat
      };
    },
    methods: {
      submitPost() {
        const newPost = {
          title: this.title,
          content: this.content,
          user: { id: this.userId },
          status: 'PENDING'
        };
  
        createPost(newPost)
          .then(response => {
            console.log("Postare creată:", response.data);
            this.$router.push('/posts');
          })
          .catch(error => {
            console.error("Eroare la crearea postării:", error);
          });
      }
    }
  };
  </script>
  