<template>
  <div>
    <h1>Lista Postări</h1>
    <ul>
      <li v-for="post in posts" :key="post.id">{{ post.title }}</li>
    </ul>
  </div>
</template>

<script>
import { getPosts } from '../services/api'; // Importă funcția specifică

export default {
  data() {
    return {
      posts: [],
    };
  },
  methods: {
    fetchPosts() {
      getPosts()
        .then(response => {
          this.posts = response.data;
        })
        .catch(error => {
          console.error('Eroare la preluarea postărilor:', error);
        });
    }
  },
  mounted() {
    this.fetchPosts();
  }
}

</script>

<style scoped>
